/**=====================
     01. General CSS Start
==========================**/
html {
  max-width: 100vw;
  overflow-x: hidden;
}

body {
  font-size: 14px;
  overflow-x: hidden;
  font-family: "Montserrat", sans-serif;
  color: #242934;
  background-color: #f5f7fb;
}

ul {
  padding-left: 0px;
  list-style-type: none;
  margin-bottom: 0;
  padding-right: 0px;
}

a {
  color: #665ed5;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

.btn-link {
  text-decoration: none;
}

* a {
  color: #665ed5;
  text-decoration: none;
}
* a:hover {
  text-decoration: none;
}

*.btn:focus {
  box-shadow: none !important;
}

h6 {
  font-weight: 600;
}

p {
  font-size: 14px;
  line-height: 1.7;
  letter-spacing: inherit;
}

span {
  font-weight: 500;
}

.dropdown-item:active, .dropdown-item.active {
  background-color: #665ed5;
}

hr {
  background-color: #e6edef;
  opacity: 1;
}

.theme-form .col-form-label {
  color: #59667a;
}

.form-label {
  color: #59667a;
  font-weight: 500;
}

.figure.text-center blockquote {
  border: none !important;
}
.figure.text-end blockquote {
  border-left: none !important;
  border-right: 4px solid #665ed5;
}
[dir=rtl] .figure.text-end blockquote {
  border-right: none;
  border-left: 4px solid #665ed5 !important;
}

.navs-icon {
  padding: 30px;
}
.navs-icon li {
  padding: 9px 0;
  font-family: system-ui;
}
.navs-icon li ul {
  padding-top: 9px;
}

.pl-navs-inline {
  padding-left: 30px !important;
}

.icon-last .form-inline .form-group {
  display: flex;
}

code {
  color: #24695c !important;
  background-color: rgba(36, 105, 92, 0.03);
  padding: 3px;
  margin: 0 3px;
  border-radius: 2px;
}

blockquote {
  border-left: 4px solid #665ed5;
  padding: 15px;
}
[dir=rtl] blockquote {
  border-left: none;
  border-right: 4px solid #665ed5;
}

blockquote.text-center {
  border: none;
  padding: 15px;
}

blockquote.text-end {
  border-left: none;
  border-right: 4px solid #e6edef;
  padding: 15px;
}

:focus {
  outline: none;
}

.disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

svg {
  vertical-align: baseline;
}

input:focus {
  outline-color: transparent;
}

.font-nunito {
  font-family: Nunito, sans-serif;
}

.font-roboto {
  font-family: "Roboto", sans-serif;
}

.font-rubik {
  font-family: "Rubik", sans-serif, sans-serif;
}

.media-widgets .media-body {
  margin-bottom: 30px;
}

.rating-star {
  display: flex;
  align-items: center;
}
.rating-star li i {
  color: #f4c705;
}

@keyframes rotateInSetting {
  0% {
    transform-origin: center;
    transform: rotate(-200deg);
  }
  to {
    transform-origin: center;
    transform: none;
  }
}
.setting-primary, .setting-secondary, .setting-white {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
}
.setting-primary svg, .setting-secondary svg, .setting-white svg {
  width: 15px;
  stroke-width: 2.5px;
  animation: rotateInSetting 1.5s ease infinite;
}

.setting-primary {
  background-color: rgba(102, 94, 213, 0.1);
}
.setting-primary svg {
  color: #665ed5;
}
.setting-primary i {
  color: #665ed5;
}
.setting-primary:hover {
  background-color: #665ed5;
}
.setting-primary:hover svg {
  color: #fff;
}
.setting-primary:hover i {
  color: #fff;
}

.setting-secondary {
  background-color: rgba(209, 209, 233, 0.1);
}
.setting-secondary svg {
  color: #d1d1e9;
}
.setting-secondary i {
  color: #d1d1e9;
}
.setting-secondary:hover {
  background-color: #d1d1e9;
}
.setting-secondary:hover svg {
  color: #fff;
}
.setting-secondary:hover i {
  color: #fff;
}

.setting-white {
  background-color: rgba(255, 255, 255, 0.1);
}
.setting-white svg {
  color: #fff;
}
.setting-white i {
  color: #fff;
}
.setting-white:hover {
  background-color: #fff;
}
.setting-white:hover svg {
  color: #665ed5;
}
.setting-white:hover i {
  color: #665ed5;
}

.setting-bg-primary {
  background-color: rgba(102, 94, 213, 0.1);
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
}
.setting-bg-primary svg {
  width: 15px;
  color: #665ed5;
  stroke-width: 2.5px;
  animation: rotateInSetting 1.5s ease infinite;
}

.typography h1, .typography .h1,
.typography h2, .typography .h2,
.typography h3, .typography .h3,
.typography h4, .typography .h4,
.typography h5, .typography .h5,
.typography h6, .typography .h6 {
  margin: 20px 0;
  margin-top: 0;
}
.typography h1:last-child, .typography .h1:last-child,
.typography h2:last-child, .typography .h2:last-child,
.typography h3:last-child, .typography .h3:last-child,
.typography h4:last-child, .typography .h4:last-child,
.typography h5:last-child, .typography .h5:last-child,
.typography h6:last-child, .typography .h6:last-child {
  margin-bottom: 0;
}
.typography small {
  padding-left: 10px;
  color: #2c323f;
}

pre {
  background-color: rgba(36, 105, 92, 0.03);
  padding: 20px 0;
}

a:hover {
  text-decoration: none;
  color: #665ed5;
}

.typography .blockquote-footer {
  background-color: #f5f7fb;
}

.owl-theme .owl-dots .owl-dot span {
  background: #b3afea;
}
.owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span {
  background: #665ed5;
}
.owl-theme .owl-nav.disabled + .owl-dots {
  margin-bottom: -10px;
  margin-top: 25px;
}

.click2edit ~ .note-editor.note-frame {
  margin-bottom: 30px;
}

.note-editor p {
  color: #999;
}
.note-editor.note-frame {
  border-color: #e6edef;
}
.note-editor i {
  color: #665ed5;
}
.note-editor span {
  color: #665ed5;
}

.note-editor.note-frame {
  border-color: #e6edef;
}

.modal .modal-body .card {
  box-shadow: none !important;
}

.modal-dialog .modal-content .modal-body p a {
  margin-right: 0;
}

.clipboaard-container p {
  font-weight: 600;
}
.clipboaard-container h6 {
  line-height: 1.8;
}

.alert-theme button {
  top: 24px !important;
  right: 30px !important;
  color: #665ed5;
}
.alert-theme span + span + span {
  border-left: 5px solid #665ed5;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 14px -3px rgba(0, 0, 0, 0.2);
}
.alert-theme i {
  padding: 20px;
  padding-right: 0 !important;
  margin-right: 10px !important;
}
.alert-theme .progress {
  margin-top: -2px !important;
  height: 2px;
}
.alert-theme .progress .progress-bar {
  background-color: #665ed5;
}

.alert-copy {
  border: 1px solid #665ed5;
  line-height: 1;
  display: inline-block;
  width: 300px;
  color: #fff;
  background-color: #665ed5;
  box-shadow: 3px 3px 5px 0 #9b9b9b;
}
.alert-copy .close {
  box-shadow: none;
  color: #fff;
  line-height: 1.4;
  font-weight: 100;
}

.ace-editor {
  height: 400px;
  width: 100%;
}

.editor-toolbar {
  width: 100% !important;
}

.CodeMirror {
  top: 0 !important;
}

.border {
  border: 1px solid #e6edef !important;
}

.irs-from, .irs-to, .irs-single {
  background: #665ed5;
}

.media {
  display: flex;
  align-items: flex-start;
}

.media-body {
  flex: 1;
}

.row > div {
  position: relative;
}

[class*=col-] {
  position: relative;
}

.cke_top {
  background-color: #f5f7fb;
}

.cke_wysiwyg_frame, .cke_wysiwyg_div {
  background-color: #f5f7fb;
}

.modal-header .btn-close, .modal-footer .btn-close, .modal-content .btn-close {
  position: absolute;
  top: 30px;
  right: 25px;
}

.btn-close:focus {
  box-shadow: none;
}

.my-gallery.gallery-with-description img {
  border-color: #e6edef !important;
}

