@media (max-width: 991px) {
.landing-wrraper{
	.landing-home-contain{
		.landing-button{
			justify-content: center;
		}
	}
}  
  .landing-wrraper {
    .landing-home .landing-home-contain {
      background-color: rgba(255, 255, 255, 0.57);
      padding: 30px 0;
      border-radius: 20px;
    }
    .btn-landing {
      padding: 10px 25px;
    }
    //landing header//

    .landing-header {
      padding-top: 15px;
      padding-bottom: 15px;
      ul.landing-menu {
        position: fixed;
        top: 0;
        right: -320px;
        width: 300px;
        height: 100vh;
        background-color: $white;
        display: block;
        transition: all 0.5s ease;
        &.open {
          right: 0;
        }
        li.nav-item {
          display: block;
          background-color: $white;
          text-align: left;
          &.menu-back {
            display: block;
          }
          .form-inline {
            .nav-link {
              padding: 5px 15px;
            }
          }
        }
      }
      .buy-block {
        .toggle-menu {
          display: block;
        }
      }
    }

    //landing home //
    .landing-home {
      height: 78vh;
      overflow: hidden;
      .landing-home-contain {
        height: 58vh;
        justify-content: center;
        display: flex;
        width: 100%;
        text-align: center;
        h2 {
          font-size: 24px;
        }
        p {
          width: 80%;
          margin-left: auto;
          margin-right: auto;
          font-size: 14px;
          margin-bottom: 25px;
        }
        // .btn-landing{
        // 	margin-left:auto;
        // 	margin-right:auto;
        // }
      }
      .position-block {
        ul {
          width: 100%;
          text-align: center;
          padding: 30px;
          top: 0;
          height: 100%;
          .img-parten {
            display: none;
          }
          li {
            position: absolute;
            width: 100%;
            img {
              margin-left: unset;
            }
            > div {
              display: flex;
              justify-content: space-between;
              align-items: flex-end;
            }
            &:first-child {
              top: 13%;
            }
            &:nth-child(2) {
              top: 33%;
            }
            &:last-child {
              bottom: 20px;
            }
          }
        }

        .img1,
        .img2,
        .img3,
        .img4,
        .img5,
        .img6,
        .img7,
        .img8,
        .img9 {
          position: absolute;
        }
        .img1 {
          top: 0;
          left: 3%;
        }
        .img2 {
          top: 0;
          left: 20%;
        }
        .img3 {
          top: 0;
          right: 40%;
        }
        .img4 {
          top: 0;
          right: 10%;
        }
        .img5 {
          height: 120px;
          top: -12px;
          left: -20px;
        }
        .img6 {
          top: 45%;
          right: -20px;
          height: 108px;
        }
        .img7 {
          bottom: 0;
          left: 5%;
        }
        .img8 {
          bottom: 0;
          right: 45%;
        }
        .img9 {
          bottom: 0;
          right: 10%;
        }
      }
    }

    // framework //
    .framework {
      .nav {
        margin-bottom: 30px;
      }
      ul.framworks-list {
        li {
          width: 150px;
          height: 150px;
          img {
            height: 40px;
          }
        }
      }
    }
  }
}
